const regex = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/;
import {
	appRegist,
	sendMSM
} from '../../../api/auth.js'
export default {
	data() {
		return {
			username: null, //用户名
			password: null, //密码
			nickName: null, //昵称
			code: null, //验证码
			codeStatus: false, //验证码发送状态，默认为不发送，即允许发送
			num: null, //秒数
		}
	},
	methods: {
		register() {
			if (!regex.test(this.account)) {
				uni.showToast({
					icon: 'none',
					title: '请填写正确的手机号'
				});
				return;
			}
			if (this.password.length < 6) {
				uni.showToast({
					icon: 'none',
					title: '密码最短为 6 个字符'
				});
				return;
			}
			if (!this.code) {
				uni.showToast({
					icon: 'none',
					title: '请填写验证码'
				});
				return;
			}
			const register = {
				telephone: this.account,
				code: this.code,
				password: this.password
			}
			appRegist(register).then(data => {
				if (data.errno == 200) {
					uni.showToast({
						title: '注册成功'
					});
					uni.navigateBack({
						delta: 1
					});
				}
			}).catch(err => {})
		},
		sendCode() {
			if (!regex.test(this.account)) {
				uni.showToast({
					icon: 'none',
					title: '请填写正确的手机号'
				});
				return;
			}
			if (!this.codeStatus) {
				//默认给60秒
				this.num = 60;
				this.codeStatus = true;
				sendMSM({
					telephone: this.account,
					need: 'notExist'
				}).then(data => {
					if (data.errno == 200) {
						uni.showToast({
							icon: 'none',
							title: '发送成功，注意查收'
						})
					} else {
						uni.showToast({
							icon: 'none',
							title: `发送失败,${data.errmsg}`
						})
					}
				}).catch(err => {
					uni.showToast({
						icon: 'none',
						title: `发送失败,${err}`
					})
				})
			} else {
				uni.showToast({
					icon: 'none',
					title: '请耐心等待'
				});
			}
		}
	},
	watch: {
		num(newVal, oldVal) {
			if (newVal === 0) {
				this.codeStatus = false;
				return;
			}
			setTimeout(() => {
				this.num--;
			}, 1000)
		}
	}
}
